import { Radio as MUIRadio } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Radio.
*/
const Radio = (props) => {
    const {
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIRadio {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
        onChange={ (e) => setProps({ value: e.target.value }) }
    />
}

Radio.defaultProps = {
    n_clicks: 0,
}

