import { FormControlUnstyled as MUIFormControlUnstyled } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI FormControlUnstyled.
*/
const FormControlUnstyled = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIFormControlUnstyled {...propsToForward}
        onChange={ (e) => setProps({ value: e.target.value }) }
    >
        {children}
    </MUIFormControlUnstyled>;
}

FormControlUnstyled.defaultProps = {
}

FormControlUnstyled.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * The content of the component.
     */
    children: PropTypes.node,

    /**
     * Class name applied to the root element.
     */
    className: PropTypes.string,

    /**
     * The components used for each slot inside the FormControl.
     * Either a string to use a HTML element or a component.
     */
    components: PropTypes.any,

    /**
     */
    componentsProps: PropTypes.any,

    /**
     */
    defaultValue: PropTypes.any,

    /**
     * If `true`, the label, input and helper text should be displayed in a disabled state.
     */
    disabled: PropTypes.bool,

    /**
     * If `true`, the label is displayed in an error state.
     */
    error: PropTypes.bool,

    /**
     * Extra properties to be placed on the FormControlContext.
     */
    extraContextProperties: PropTypes.any,

    /**
     * If `true`, the component is displayed in focused state.
     */
    focused: PropTypes.bool,

    /**
     * If `true`, the label will indicate that the `input` is required.
     */
    required: PropTypes.bool,

    /**
     */
    value: PropTypes.any,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default FormControlUnstyled;
