import { ToggleButtonGroup as MUIToggleButtonGroup } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';
import { Icon } from '@mui/material';
import { ToggleButton as MUIToggleButton } from '@mui/material';

/**
* Material-UI ToggleButtonGroup.
*/
const ToggleButtonGroup = (props) => {
    const {
        children,
        persistence,
        persisted_props,
        persistence_type,
        buttons,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIToggleButtonGroup {...propsToForward}
        onChange={ (e, value) => setProps({ value }) }
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    >
        {Object.keys(buttons).map((b) => (<MUIToggleButton value={b}><Icon>{buttons[b]}</Icon></MUIToggleButton>))}
    </MUIToggleButtonGroup>;
}

ToggleButtonGroup.defaultProps = {
    persisted_props: ['value'],
    persistence_type: 'local',
    buttons: {},
    n_clicks: 0,
}

ToggleButtonGroup.propTypes = {
    /**
     * The content of the component.
     */
    children: PropTypes.node,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * The color of a button when it is selected.
     */
    color: PropTypes.oneOf(["success", "info", "warning", "error", "standard", "primary", "secondary"]),

    /**
     * If `true`, only allow one of the child ToggleButton values to be selected.
     */
    exclusive: PropTypes.bool,

    /**
     * If `true`, the component is disabled. This implies that all ToggleButton children will be disabled.
     */
    disabled: PropTypes.bool,

    /**
     * If `true`, the button group will take up the full width of its container.
     */
    fullWidth: PropTypes.bool,

    /**
     * The component orientation (layout flow direction).
     */
    orientation: PropTypes.oneOf(["horizontal", "vertical"]),

    /**
     * The size of the component.
     */
    size: PropTypes.oneOf(["small", "medium", "large"]),

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The currently selected value within the group or an array of selected
     * values when `exclusive` is false.
     * 
     * The value must have reference equality with the option in order to be selected.
     */
    value: PropTypes.any,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     */
    slot: PropTypes.string,

    /**
     */
    title: PropTypes.string,

    /**
     */
    defaultChecked: PropTypes.bool,

    /**
     */
    defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any]),

    /**
     */
    suppressContentEditableWarning: PropTypes.bool,

    /**
     */
    suppressHydrationWarning: PropTypes.bool,

    /**
     */
    accessKey: PropTypes.string,

    /**
     */
    contentEditable: PropTypes.oneOf([false, true, "true", "false", "inherit"]),

    /**
     */
    contextMenu: PropTypes.string,

    /**
     */
    dir: PropTypes.string,

    /**
     */
    draggable: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    hidden: PropTypes.bool,

    /**
     */
    id: PropTypes.string,

    /**
     */
    lang: PropTypes.string,

    /**
     */
    placeholder: PropTypes.string,

    /**
     */
    spellCheck: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    tabIndex: PropTypes.number,

    /**
     */
    translate: PropTypes.oneOf(["yes", "no"]),

    /**
     */
    radioGroup: PropTypes.string,

    /**
     */
    role: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf(["article", "button", "dialog", "figure", "form", "img", "link", "main", "menu", "menuitem", "option", "table", "switch", "alert", "alertdialog", "application", "banner", "cell", "checkbox", "columnheader", "combobox", "complementary", "contentinfo", "definition", "directory", "document", "feed", "grid", "gridcell", "group", "heading", "list", "listbox", "listitem", "log", "marquee", "math", "menubar", "menuitemcheckbox", "menuitemradio", "navigation", "none", "note", "presentation", "progressbar", "radio", "radiogroup", "region", "row", "rowgroup", "rowheader", "scrollbar", "search", "searchbox", "separator", "slider", "spinbutton", "status", "tab", "tablist", "tabpanel", "term", "textbox", "timer", "toolbar", "tooltip", "tree", "treegrid", "treeitem"])]),

    /**
     */
    about: PropTypes.string,

    /**
     */
    datatype: PropTypes.string,

    /**
     */
    inlist: PropTypes.any,

    /**
     */
    prefix: PropTypes.string,

    /**
     */
    property: PropTypes.string,

    /**
     */
    resource: PropTypes.string,

    /**
     */
    typeof: PropTypes.string,

    /**
     */
    vocab: PropTypes.string,

    /**
     */
    autoCapitalize: PropTypes.string,

    /**
     */
    autoCorrect: PropTypes.string,

    /**
     */
    autoSave: PropTypes.string,

    /**
     */
    itemProp: PropTypes.string,

    /**
     */
    itemScope: PropTypes.bool,

    /**
     */
    itemType: PropTypes.string,

    /**
     */
    itemID: PropTypes.string,

    /**
     */
    itemRef: PropTypes.string,

    /**
     */
    results: PropTypes.number,

    /**
     */
    security: PropTypes.string,

    /**
     */
    unselectable: PropTypes.oneOf(["on", "off"]),

    /**
     * Hints at the type of data that might be entered by the user while editing the element or its contents
     */
    inputMode: PropTypes.oneOf(["text", "none", "search", "tel", "url", "email", "numeric", "decimal"]),

    /**
     * Specify that a standard HTML element should behave like a defined custom built-in element
     */
    is: PropTypes.string,

    /**
     */
    dangerouslySetInnerHTML: PropTypes.any,

    /**
     */
    ref: PropTypes.any,

    /**
     * Used to allow user interactions in this component to be persisted when
 the component - or the page - is refreshed. If `persisted` is truthy and
 hasn't changed from its previous value, a `value` that the user has
 changed while using the app will keep that change, as long as
 the new `value` also matches what was given originally.
Used in conjunction with `persistence_type`.
     */
    persistence: PropTypes.oneOfType([PropTypes.bool, PropTypes.string, PropTypes.number]),

    /**
     * Properties whose user interactions will persist after refreshing the
 component or the page. Since only `value` is allowed this prop can
normally be ignored.
     */
    persisted_props: PropTypes.arrayOf(PropTypes.oneOf(['value'])),

    /**
     * Where persisted user changes will be stored:
 memory: only kept in memory, reset on page refresh.
 local: window.localStorage, data is kept after the browser quit.
 session: window.sessionStorage, data is cleared once the browser quit.
 location: window.location, data appears in the URL and can be shared with others.
     */
    persistence_type: PropTypes.oneOf(['local', 'session', 'memory', 'location']),

    /**
     * A collection of buttons, where keys are the 'values' and values are icon names.
     */
    buttons: PropTypes.object,

    /**
     * An integer that represents the number of times that this element has been clicked on.
     */
    n_clicks: PropTypes.number,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default ToggleButtonGroup;
