import { Unstable_TrapFocus as MUIUnstable_TrapFocus } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Unstable_TrapFocus.
*/
const Unstable_TrapFocus = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIUnstable_TrapFocus {...propsToForward}
    >
        {children}
    </MUIUnstable_TrapFocus>;
}

Unstable_TrapFocus.defaultProps = {
}

Unstable_TrapFocus.propTypes = {
    /**
     * If `true`, focus is locked.
     */
    open: PropTypes.bool,

    /**
     * A single child content element.
     */
    children: PropTypes.any,

    /**
     * If `true`, the trap focus will not automatically shift focus to itself when it opens, and
     * replace it to the last focused element when it closes.
     * This also works correctly with any trap focus children that have the `disableAutoFocus` prop.
     * 
     * Generally this should never be set to `true` as it makes the trap focus less
     * accessible to assistive technologies, like screen readers.
     */
    disableAutoFocus: PropTypes.bool,

    /**
     * If `true`, the trap focus will not prevent focus from leaving the trap focus while open.
     * 
     * Generally this should never be set to `true` as it makes the trap focus less
     * accessible to assistive technologies, like screen readers.
     */
    disableEnforceFocus: PropTypes.bool,

    /**
     * If `true`, the trap focus will not restore focus to previously focused element once
     * trap focus is hidden.
     */
    disableRestoreFocus: PropTypes.bool,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Unstable_TrapFocus;
