import { Select as MUISelect } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';
import { MenuItem as MUIMenuItem } from '@mui/material';

/**
* Material-UI Select.
*/
const Select = (props) => {
    const {
        children,
        persistence,
        persisted_props,
        persistence_type,
        options,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUISelect {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
        onChange={ (e) => setProps({ value: e.target.value }) }
    >
        {Object.keys(options).map((o) => (<MUIMenuItem value={o}>{options[o]}</MUIMenuItem>))}
    </MUISelect>;
}

Select.defaultProps = {
    persisted_props: ['value'],
    persistence_type: 'local',
    value: '',
    options: [],
    n_clicks: 0,
}

