import { SliderUnstyled as MUISliderUnstyled } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI SliderUnstyled.
*/
const SliderUnstyled = (props) => {
    const {
        setProps,
        ...propsToForward
    } = props;


    return <MUISliderUnstyled {...propsToForward}
        onChange={ (e) => setProps({ value: e.target.value }) }
    />
}

SliderUnstyled.defaultProps = {
}

SliderUnstyled.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * The components used for each slot inside the Slider.
     * Either a string to use a HTML element or a component.
     */
    components: PropTypes.any,

    /**
     * The props used for each slot inside the Slider.
     */
    componentsProps: PropTypes.any,

    /**
     * The default value. Use when the component is not controlled.
     */
    defaultValue: PropTypes.oneOfType([PropTypes.number, PropTypes.arrayOf(PropTypes.number)]),

    /**
     * If `true`, the component is disabled.
     */
    disabled: PropTypes.bool,

    /**
     * If `true`, the active thumb doesn't swap when moving pointer over a thumb while dragging another thumb.
     */
    disableSwap: PropTypes.bool,

    /**
     * Indicates whether the theme context has rtl direction. It is set automatically.
     */
    isRtl: PropTypes.bool,

    /**
     * Marks indicate predetermined values to which the user can move the slider.
     * If `true` the marks are spaced according the value of the `step` prop.
     * If an array, it should contain objects with `value` and an optional `label` keys.
     */
    marks: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.any), PropTypes.oneOf([false, true])]),

    /**
     * The maximum allowed value of the slider.
     * Should not be equal to min.
     */
    max: PropTypes.number,

    /**
     * The minimum allowed value of the slider.
     * Should not be equal to max.
     */
    min: PropTypes.number,

    /**
     * Name attribute of the hidden `input` element.
     */
    name: PropTypes.string,

    /**
     * The component orientation.
     */
    orientation: PropTypes.oneOf(["horizontal", "vertical"]),

    /**
     * The granularity with which the slider can step through values. (A "discrete" slider.)
     * The `min` prop serves as the origin for the valid values.
     * We recommend (max - min) to be evenly divisible by the step.
     * 
     * When step is `null`, the thumb can only be slid onto marks provided with the `marks` prop.
     */
    step: PropTypes.number,

    /**
     * Tab index attribute of the hidden `input` element.
     */
    tabIndex: PropTypes.number,

    /**
     * The track presentation:
     * 
     * - `normal` the track will render a bar representing the slider value.
     * - `inverted` the track will render a bar representing the remaining slider value.
     * - `false` the track will render without a bar.
     */
    track: PropTypes.oneOf([false, "normal", "inverted"]),

    /**
     * The value of the slider.
     * For ranged sliders, provide an array with two values.
     */
    value: PropTypes.oneOfType([PropTypes.number, PropTypes.arrayOf(PropTypes.number)]),

    /**
     * Controls when the value label is displayed:
     * 
     * - `auto` the value label will display when the thumb is hovered or focused.
     * - `on` will display persistently.
     * - `off` will never display.
     */
    valueLabelDisplay: PropTypes.oneOf(["on", "off", "auto"]),

    /**
     * The format function the value label's value.
     * 
     * When a function is provided, it should have the following signature:
     * 
     * - {number} value The value label's value to format
     * - {number} index The value label's index to format
     */
    valueLabelFormat: PropTypes.string,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default SliderUnstyled;
