import { PaginationItem as MUIPaginationItem } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI PaginationItem.
*/
const PaginationItem = (props) => {
    const {
        setProps,
        ...propsToForward
    } = props;


    return <MUIPaginationItem {...propsToForward}
    />
}

PaginationItem.defaultProps = {
}

PaginationItem.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * The active color.
     */
    color: PropTypes.oneOf(["standard", "primary", "secondary"]),

    /**
     * If `true`, the component is disabled.
     */
    disabled: PropTypes.bool,

    /**
     * The current page number.
     */
    page: PropTypes.any,

    /**
     * If `true` the pagination item is selected.
     */
    selected: PropTypes.bool,

    /**
     * The shape of the pagination item.
     */
    shape: PropTypes.oneOf(["rounded", "circular"]),

    /**
     * The size of the component.
     */
    size: PropTypes.oneOf(["small", "medium", "large"]),

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The type of pagination item.
     */
    type: PropTypes.oneOf(["page", "next", "previous", "first", "last", "start-ellipsis", "end-ellipsis"]),

    /**
     * The variant to use.
     */
    variant: PropTypes.oneOf(["text", "outlined"]),

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default PaginationItem;
