import { FilledInput as MUIFilledInput } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI FilledInput.
*/
const FilledInput = (props) => {
    const {
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIFilledInput {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
        onChange={ (e) => setProps({ value: e.target.value }) }
    />
}

FilledInput.defaultProps = {
    n_clicks: 0,
}

