import { SliderValueLabelUnstyled as MUISliderValueLabelUnstyled } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI SliderValueLabelUnstyled.
*/
const SliderValueLabelUnstyled = (props) => {
    const {
        setProps,
        ...propsToForward
    } = props;


    return <MUISliderValueLabelUnstyled {...propsToForward}
        onChange={ (e) => setProps({ value: e.target.value }) }
    />
}

SliderValueLabelUnstyled.defaultProps = {
}

SliderValueLabelUnstyled.propTypes = {
    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * The components used for each slot inside the ValueLabel.
     * Either a string to use a HTML element or a component.
     */
    components: PropTypes.any,

    /**
     * The value of the slider.
     * For ranged sliders, provide an array with two values.
     */
    value: PropTypes.oneOfType([PropTypes.number, PropTypes.arrayOf(PropTypes.number)]),

    /**
     * Controls when the value label is displayed:
     * 
     * - `auto` the value label will display when the thumb is hovered or focused.
     * - `on` will display persistently.
     * - `off` will never display.
     */
    valueLabelDisplay: PropTypes.oneOf(["on", "off", "auto"]),

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default SliderValueLabelUnstyled;
