import { ButtonUnstyled as MUIButtonUnstyled } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI ButtonUnstyled.
*/
const ButtonUnstyled = (props) => {
    const {
        setProps,
        ...propsToForward
    } = props;


    return <MUIButtonUnstyled {...propsToForward}
    />
}

ButtonUnstyled.defaultProps = {
}

ButtonUnstyled.propTypes = {
    /**
     */
    ref: PropTypes.any,

    /**
     */
    key: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default ButtonUnstyled;
