import { Backdrop as MUIBackdrop } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Backdrop.
*/
const Backdrop = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIBackdrop {...propsToForward}
    >
        {children}
    </MUIBackdrop>;
}

Backdrop.defaultProps = {
}

Backdrop.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     */
    ref: PropTypes.any,

    /**
     * If `true`, the component will transition in.
     */
    in: PropTypes.bool,

    /**
     * By default the child component is mounted immediately along with the
     * parent Transition component. If you want to "lazy mount" the component on
     * the first `in={true}` you can set `mountOnEnter`. After the first enter
     * transition the component will stay mounted, even on "exited", unless you
     * also specify `unmountOnExit`.
     */
    mountOnEnter: PropTypes.bool,

    /**
     * By default the child component stays mounted after it reaches the
     * 'exited' state. Set `unmountOnExit` if you'd prefer to unmount the
     * component after it finishes exiting.
     */
    unmountOnExit: PropTypes.bool,

    /**
     * The duration for the transition, in milliseconds.
     * You may specify a single timeout for all transitions, or individually with an object.
     */
    timeout: PropTypes.oneOfType([PropTypes.number, PropTypes.any]),

    /**
     * The transition timing function.
     * You may specify a single easing or a object containing enter and exit values.
     */
    easing: PropTypes.oneOfType([PropTypes.string, PropTypes.any]),

    /**
     * Enable or disable enter transitions.
     */
    enter: PropTypes.bool,

    /**
     * Perform the enter transition when it first mounts if `in` is also `true`.
     * Set this to `false` to disable this behavior.
     */
    appear: PropTypes.bool,

    /**
     * Enable or disable exit transitions.
     */
    exit: PropTypes.bool,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * If `true`, the component is shown.
     */
    open: PropTypes.bool,

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The duration for the transition, in milliseconds.
     * You may specify a single timeout for all transitions, or individually with an object.
     */
    transitionDuration: PropTypes.oneOfType([PropTypes.number, PropTypes.any]),

    /**
     * The content of the component.
     */
    children: PropTypes.node,

    /**
     * The components used for each slot inside the Backdrop.
     * Either a string to use a HTML element or a component.
     */
    components: PropTypes.any,

    /**
     * The props used for each slot inside the Backdrop.
     */
    componentsProps: PropTypes.any,

    /**
     * If `true`, the backdrop is invisible.
     * It can be used when rendering a popover or a custom select component.
     */
    invisible: PropTypes.bool,

    /**
     */
    className: PropTypes.string,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Backdrop;
