import { Popper as MUIPopper } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Popper.
*/
const Popper = (props) => {
    const {
        children,
        anchorEl,
        container,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;

    propsToForward.anchorEl = document.getElementById(anchorEl);
    propsToForward.container = document.getElementById(container);

    return <MUIPopper {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    >
        {children}
    </MUIPopper>;
}

Popper.defaultProps = {
    n_clicks: 0,
}

Popper.propTypes = {
    /**
     */
    ref: PropTypes.any,

    /**
     * An HTML element, [virtualElement](https://popper.js.org/docs/v2/virtual-elements/),
     * or a function that returns either.
     * It's used to set the position of the popper.
     * The return value will passed as the reference object of the Popper instance.
     * If specified, the ID of an existing element should be provided.
     */
    anchorEl: PropTypes.string,

    /**
     * Popper render function or node.
     */
    children: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any, PropTypes.oneOf([false, true])]),

    /**
     * An HTML element or function that returns one.
     * The `container` will have the portal children appended to it.
     * 
     * By default, it uses the body of the top-level document object,
     * so it's simply `document.body` most of the time.
     * If specified, the ID of an existing element should be provided.
     */
    container: PropTypes.string,

    /**
     * The `children` will be under the DOM hierarchy of the parent component.
     */
    disablePortal: PropTypes.bool,

    /**
     * Always keep the children in the DOM.
     * This prop can be useful in SEO situation or
     * when you want to maximize the responsiveness of the Popper.
     */
    keepMounted: PropTypes.bool,

    /**
     * Popper.js is based on a "plugin-like" architecture,
     * most of its features are fully encapsulated "modifiers".
     * 
     * A modifier is a function that is called each time Popper.js needs to
     * compute the position of the popper.
     * For this reason, modifiers should be very performant to avoid bottlenecks.
     * To learn how to create a modifier, [read the modifiers documentation](https://popper.js.org/docs/v2/modifiers/).
     */
    modifiers: PropTypes.arrayOf(PropTypes.any),

    /**
     * If `true`, the component is shown.
     */
    open: PropTypes.bool,

    /**
     * Popper placement.
     */
    placement: PropTypes.oneOf(["bottom", "left", "right", "top", "auto", "auto-start", "auto-end", "top-start", "top-end", "bottom-start", "bottom-end", "right-start", "right-end", "left-start", "left-end"]),

    /**
     * Options provided to the [`Popper.js`](https://popper.js.org/docs/v2/constructors/#options) instance.
     */
    popperOptions: PropTypes.any,

    /**
     * A ref that points to the used popper instance.
     */
    popperRef: PropTypes.any,

    /**
     * Help supporting a react-transition-group/Transition component.
     */
    transition: PropTypes.bool,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     */
    slot: PropTypes.string,

    /**
     */
    title: PropTypes.string,

    /**
     */
    defaultChecked: PropTypes.bool,

    /**
     */
    defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any]),

    /**
     */
    suppressContentEditableWarning: PropTypes.bool,

    /**
     */
    suppressHydrationWarning: PropTypes.bool,

    /**
     */
    accessKey: PropTypes.string,

    /**
     */
    contentEditable: PropTypes.oneOf([false, true, "true", "false", "inherit"]),

    /**
     */
    contextMenu: PropTypes.string,

    /**
     */
    dir: PropTypes.string,

    /**
     */
    draggable: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    hidden: PropTypes.bool,

    /**
     */
    id: PropTypes.string,

    /**
     */
    lang: PropTypes.string,

    /**
     */
    placeholder: PropTypes.string,

    /**
     */
    spellCheck: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    tabIndex: PropTypes.number,

    /**
     */
    translate: PropTypes.oneOf(["yes", "no"]),

    /**
     */
    radioGroup: PropTypes.string,

    /**
     */
    role: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf(["article", "button", "dialog", "figure", "form", "img", "link", "main", "menu", "menuitem", "option", "table", "switch", "alert", "alertdialog", "application", "banner", "cell", "checkbox", "columnheader", "combobox", "complementary", "contentinfo", "definition", "directory", "document", "feed", "grid", "gridcell", "group", "heading", "list", "listbox", "listitem", "log", "marquee", "math", "menubar", "menuitemcheckbox", "menuitemradio", "navigation", "none", "note", "presentation", "progressbar", "radio", "radiogroup", "region", "row", "rowgroup", "rowheader", "scrollbar", "search", "searchbox", "separator", "slider", "spinbutton", "status", "tab", "tablist", "tabpanel", "term", "textbox", "timer", "toolbar", "tooltip", "tree", "treegrid", "treeitem"])]),

    /**
     */
    about: PropTypes.string,

    /**
     */
    datatype: PropTypes.string,

    /**
     */
    inlist: PropTypes.any,

    /**
     */
    prefix: PropTypes.string,

    /**
     */
    property: PropTypes.string,

    /**
     */
    resource: PropTypes.string,

    /**
     */
    typeof: PropTypes.string,

    /**
     */
    vocab: PropTypes.string,

    /**
     */
    autoCapitalize: PropTypes.string,

    /**
     */
    autoCorrect: PropTypes.string,

    /**
     */
    autoSave: PropTypes.string,

    /**
     */
    color: PropTypes.string,

    /**
     */
    itemProp: PropTypes.string,

    /**
     */
    itemScope: PropTypes.bool,

    /**
     */
    itemType: PropTypes.string,

    /**
     */
    itemID: PropTypes.string,

    /**
     */
    itemRef: PropTypes.string,

    /**
     */
    results: PropTypes.number,

    /**
     */
    security: PropTypes.string,

    /**
     */
    unselectable: PropTypes.oneOf(["on", "off"]),

    /**
     * Hints at the type of data that might be entered by the user while editing the element or its contents
     */
    inputMode: PropTypes.oneOf(["text", "none", "search", "tel", "url", "email", "numeric", "decimal"]),

    /**
     * Specify that a standard HTML element should behave like a defined custom built-in element
     */
    is: PropTypes.string,

    /**
     */
    dangerouslySetInnerHTML: PropTypes.any,

    /**
     * An integer that represents the number of times that this element has been clicked on.
     */
    n_clicks: PropTypes.number,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Popper;
