import { StepLabel as MUIStepLabel } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI StepLabel.
*/
const StepLabel = (props) => {
    const {
        children,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIStepLabel {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    >
        {children}
    </MUIStepLabel>;
}

StepLabel.defaultProps = {
    n_clicks: 0,
}

