import { Collapse as MUICollapse } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Collapse.
*/
const Collapse = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUICollapse {...propsToForward}
    >
        {children}
    </MUICollapse>;
}

Collapse.defaultProps = {
}

