import { Autocomplete as MUIAutocomplete } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Autocomplete.
*/
const Autocomplete = (props) => {
    const {
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIAutocomplete {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    />
}

Autocomplete.defaultProps = {
    n_clicks: 0,
}

Autocomplete.propTypes = {
    /**
     * Props applied to the [`Chip`](/api/chip/) element.
     */
    ChipProps: PropTypes.any,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * The icon to display in place of the default clear icon.
     */
    clearIcon: PropTypes.any,

    /**
     * Override the default text for the *clear* icon button.
     * 
     * For localization purposes, you can use the provided [translations](/guides/localization/).
     */
    clearText: PropTypes.string,

    /**
     * Override the default text for the *close popup* icon button.
     * 
     * For localization purposes, you can use the provided [translations](/guides/localization/).
     */
    closeText: PropTypes.string,

    /**
     * The props used for each slot inside.
     */
    componentsProps: PropTypes.any,

    /**
     * If `true`, the component is disabled.
     */
    disabled: PropTypes.bool,

    /**
     * If `true`, the `Popper` content will be under the DOM hierarchy of the parent component.
     */
    disablePortal: PropTypes.bool,

    /**
     * Force the visibility display of the popup icon.
     */
    forcePopupIcon: PropTypes.oneOf([false, true, "auto"]),

    /**
     * If `true`, the input will take up the full width of its container.
     */
    fullWidth: PropTypes.bool,

    /**
     * The component used to render the listbox.
     */
    ListboxComponent: PropTypes.any,

    /**
     * Props applied to the Listbox element.
     */
    ListboxProps: PropTypes.any,

    /**
     * If `true`, the component is in a loading state.
     * This shows the `loadingText` in place of suggestions (only if there are no suggestions to show, e.g. `options` are empty).
     */
    loading: PropTypes.bool,

    /**
     * Text to display when in a loading state.
     * 
     * For localization purposes, you can use the provided [translations](/guides/localization/).
     */
    loadingText: PropTypes.any,

    /**
     * The maximum number of tags that will be visible when not focused.
     * Set `-1` to disable the limit.
     */
    limitTags: PropTypes.number,

    /**
     * Text to display when there are no options.
     * 
     * For localization purposes, you can use the provided [translations](/guides/localization/).
     */
    noOptionsText: PropTypes.any,

    /**
     * Override the default text for the *open popup* icon button.
     * 
     * For localization purposes, you can use the provided [translations](/guides/localization/).
     */
    openText: PropTypes.string,

    /**
     * The component used to render the body of the popup.
     */
    PaperComponent: PropTypes.any,

    /**
     * The component used to position the popup.
     */
    PopperComponent: PropTypes.any,

    /**
     * The icon to display in place of the default popup icon.
     */
    popupIcon: PropTypes.any,

    /**
     * The size of the component.
     */
    size: PropTypes.oneOf(["small", "medium"]),

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * If `true`, the portion of the selected suggestion that has not been typed by the user,
     * known as the completion string, appears inline after the input cursor in the textbox.
     * The inline completion string is visually highlighted and has a selected state.
     */
    autoComplete: PropTypes.bool,

    /**
     * If `true`, the first option is automatically highlighted.
     */
    autoHighlight: PropTypes.bool,

    /**
     * If `true`, the selected option becomes the value of the input
     * when the Autocomplete loses focus unless the user chooses
     * a different option or changes the character string in the input.
     */
    autoSelect: PropTypes.bool,

    /**
     * Control if the input should be blurred when an option is selected:
     * 
     * - `false` the input is not blurred.
     * - `true` the input is always blurred.
     * - `touch` the input is blurred after a touch event.
     * - `mouse` the input is blurred after a mouse event.
     */
    blurOnSelect: PropTypes.oneOf([false, true, "touch", "mouse"]),

    /**
     * If `true`, the input's text is cleared on blur if no value is selected.
     * 
     * Set to `true` if you want to help the user enter a new value.
     * Set to `false` if you want to help the user resume his search.
     */
    clearOnBlur: PropTypes.bool,

    /**
     * If `true`, clear all values when the user presses escape and the popup is closed.
     */
    clearOnEscape: PropTypes.bool,

    /**
     * The component name that is using this hook. Used for warnings.
     */
    componentName: PropTypes.string,

    /**
     * If `true`, the popup won't close when a value is selected.
     */
    disableCloseOnSelect: PropTypes.bool,

    /**
     * If `true`, will allow focus on disabled items.
     */
    disabledItemsFocusable: PropTypes.bool,

    /**
     * If `true`, the list box in the popup will not wrap focus.
     */
    disableListWrap: PropTypes.bool,

    /**
     * If `true`, hide the selected options from the list box.
     */
    filterSelectedOptions: PropTypes.bool,

    /**
     * If `true`, the component handles the "Home" and "End" keys when the popup is open.
     * It should move focus to the first option and last option, respectively.
     */
    handleHomeEndKeys: PropTypes.bool,

    /**
     * This prop is used to help implement the accessibility logic.
     * If you don't provide an id it will fall back to a randomly generated one.
     */
    id: PropTypes.string,

    /**
     * If `true`, the highlight can move to the input.
     */
    includeInputInList: PropTypes.bool,

    /**
     * The input value.
     */
    inputValue: PropTypes.string,

    /**
     * If `true`, the component is shown.
     */
    open: PropTypes.bool,

    /**
     * If `true`, the popup will open on input focus.
     */
    openOnFocus: PropTypes.bool,

    /**
     * Array of options.
     */
    options: PropTypes.any,

    /**
     * If `true`, the input's text is selected on focus.
     * It helps the user clear the selected value.
     */
    selectOnFocus: PropTypes.bool,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     */
    slot: PropTypes.string,

    /**
     */
    title: PropTypes.string,

    /**
     */
    defaultChecked: PropTypes.bool,

    /**
     */
    suppressContentEditableWarning: PropTypes.bool,

    /**
     */
    suppressHydrationWarning: PropTypes.bool,

    /**
     */
    accessKey: PropTypes.string,

    /**
     */
    contentEditable: PropTypes.oneOf([false, true, "true", "false", "inherit"]),

    /**
     */
    contextMenu: PropTypes.string,

    /**
     */
    dir: PropTypes.string,

    /**
     */
    draggable: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    hidden: PropTypes.bool,

    /**
     */
    lang: PropTypes.string,

    /**
     */
    placeholder: PropTypes.string,

    /**
     */
    spellCheck: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    tabIndex: PropTypes.number,

    /**
     */
    translate: PropTypes.oneOf(["yes", "no"]),

    /**
     */
    radioGroup: PropTypes.string,

    /**
     */
    role: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf(["article", "button", "dialog", "figure", "form", "img", "link", "main", "menu", "menuitem", "option", "table", "switch", "alert", "alertdialog", "application", "banner", "cell", "checkbox", "columnheader", "combobox", "complementary", "contentinfo", "definition", "directory", "document", "feed", "grid", "gridcell", "group", "heading", "list", "listbox", "listitem", "log", "marquee", "math", "menubar", "menuitemcheckbox", "menuitemradio", "navigation", "none", "note", "presentation", "progressbar", "radio", "radiogroup", "region", "row", "rowgroup", "rowheader", "scrollbar", "search", "searchbox", "separator", "slider", "spinbutton", "status", "tab", "tablist", "tabpanel", "term", "textbox", "timer", "toolbar", "tooltip", "tree", "treegrid", "treeitem"])]),

    /**
     */
    about: PropTypes.string,

    /**
     */
    datatype: PropTypes.string,

    /**
     */
    inlist: PropTypes.any,

    /**
     */
    prefix: PropTypes.string,

    /**
     */
    property: PropTypes.string,

    /**
     */
    resource: PropTypes.string,

    /**
     */
    typeof: PropTypes.string,

    /**
     */
    vocab: PropTypes.string,

    /**
     */
    autoCapitalize: PropTypes.string,

    /**
     */
    autoCorrect: PropTypes.string,

    /**
     */
    autoSave: PropTypes.string,

    /**
     */
    color: PropTypes.string,

    /**
     */
    itemProp: PropTypes.string,

    /**
     */
    itemScope: PropTypes.bool,

    /**
     */
    itemType: PropTypes.string,

    /**
     */
    itemID: PropTypes.string,

    /**
     */
    itemRef: PropTypes.string,

    /**
     */
    results: PropTypes.number,

    /**
     */
    security: PropTypes.string,

    /**
     */
    unselectable: PropTypes.oneOf(["on", "off"]),

    /**
     * Hints at the type of data that might be entered by the user while editing the element or its contents
     */
    inputMode: PropTypes.oneOf(["text", "none", "search", "tel", "url", "email", "numeric", "decimal"]),

    /**
     * Specify that a standard HTML element should behave like a defined custom built-in element
     */
    is: PropTypes.string,

    /**
     */
    dangerouslySetInnerHTML: PropTypes.any,

    /**
     */
    ref: PropTypes.any,

    /**
     * An integer that represents the number of times that this element has been clicked on.
     */
    n_clicks: PropTypes.number,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Autocomplete;
