import { Tabs as MUITabs } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Tabs.
*/
const Tabs = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUITabs {...propsToForward}
        onChange={ (e) => setProps({ value: e.target.value }) }
    >
        {children}
    </MUITabs>;
}

Tabs.defaultProps = {
}

Tabs.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * Callback fired when the component mounts.
     * This is useful when you want to trigger an action programmatically.
     * It supports two actions: `updateIndicator()` and `updateScrollButtons()`
     */
    action: PropTypes.any,

    /**
     * If `true`, the scroll buttons aren't forced hidden on mobile.
     * By default the scroll buttons are hidden on mobile and takes precedence over `scrollButtons`.
     */
    allowScrollButtonsMobile: PropTypes.bool,

    /**
     * If `true`, the tabs are centered.
     * This prop is intended for large views.
     */
    centered: PropTypes.bool,

    /**
     * The content of the component.
     */
    children: PropTypes.node,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * Determines the color of the indicator.
     */
    indicatorColor: PropTypes.oneOf(["primary", "secondary"]),

    /**
     * The component orientation (layout flow direction).
     */
    orientation: PropTypes.oneOf(["horizontal", "vertical"]),

    /**
     * The component used to render the scroll buttons.
     */
    ScrollButtonComponent: PropTypes.oneOfType([PropTypes.any, PropTypes.oneOf(["symbol", "object", "style", "div", "a", "abbr", "address", "area", "article", "aside", "audio", "b", "base", "bdi", "bdo", "big", "blockquote", "body", "br", "button", "canvas", "caption", "cite", "code", "col", "colgroup", "data", "datalist", "dd", "del", "details", "dfn", "dialog", "dl", "dt", "em", "embed", "fieldset", "figcaption", "figure", "footer", "form", "h1", "h2", "h3", "h4", "h5", "h6", "head", "header", "hgroup", "hr", "html", "i", "iframe", "img", "input", "ins", "kbd", "keygen", "label", "legend", "li", "link", "main", "map", "mark", "menu", "menuitem", "meta", "meter", "nav", "noindex", "noscript", "ol", "optgroup", "option", "output", "p", "param", "picture", "pre", "progress", "q", "rp", "rt", "ruby", "s", "samp", "slot", "script", "section", "select", "small", "source", "span", "strong", "sub", "summary", "sup", "table", "template", "tbody", "td", "textarea", "tfoot", "th", "thead", "time", "title", "tr", "track", "u", "ul", "var", "video", "wbr", "webview", "svg", "animate", "animateMotion", "animateTransform", "circle", "clipPath", "defs", "desc", "ellipse", "feBlend", "feColorMatrix", "feComponentTransfer", "feComposite", "feConvolveMatrix", "feDiffuseLighting", "feDisplacementMap", "feDistantLight", "feDropShadow", "feFlood", "feFuncA", "feFuncB", "feFuncG", "feFuncR", "feGaussianBlur", "feImage", "feMerge", "feMergeNode", "feMorphology", "feOffset", "fePointLight", "feSpecularLighting", "feSpotLight", "feTile", "feTurbulence", "filter", "foreignObject", "g", "image", "line", "linearGradient", "marker", "mask", "metadata", "mpath", "path", "pattern", "polygon", "polyline", "radialGradient", "rect", "stop", "switch", "text", "textPath", "tspan", "use", "view"])]),

    /**
     * Determine behavior of scroll buttons when tabs are set to scroll:
     * 
     * - `auto` will only present them when not all the items are visible.
     * - `true` will always present them.
     * - `false` will never present them.
     * 
     * By default the scroll buttons are hidden on mobile.
     * This behavior can be disabled with `allowScrollButtonsMobile`.
     */
    scrollButtons: PropTypes.oneOf([false, true, "auto"]),

    /**
     * If `true` the selected tab changes on focus. Otherwise it only
     * changes on activation.
     */
    selectionFollowsFocus: PropTypes.bool,

    /**
     * Props applied to the tab indicator element.
     */
    TabIndicatorProps: PropTypes.any,

    /**
     * Props applied to the [`TabScrollButton`](/api/tab-scroll-button/) element.
     */
    TabScrollButtonProps: PropTypes.any,

    /**
     * Determines the color of the `Tab`.
     */
    textColor: PropTypes.oneOf(["inherit", "primary", "secondary"]),

    /**
     * The value of the currently selected `Tab`.
     * If you don't want any selected `Tab`, you can set this prop to `false`.
     */
    value: PropTypes.any,

    /**
     * Determines additional display behavior of the tabs:
     * 
     * - `scrollable` will invoke scrolling properties and allow for horizontally
     * scrolling (or swiping) of the tab bar.
     * -`fullWidth` will make the tabs grow to use all the available space,
     * which should be used for small views, like on mobile.
     * - `standard` will render the default state.
     */
    variant: PropTypes.oneOf(["standard", "fullWidth", "scrollable"]),

    /**
     * If `true`, the scrollbar is visible. It can be useful when displaying
     * a long vertical list of tabs.
     */
    visibleScrollbar: PropTypes.bool,

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Tabs;
