import { ClickAwayListener as MUIClickAwayListener } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI ClickAwayListener.
*/
const ClickAwayListener = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIClickAwayListener {...propsToForward}
    >
        {children}
    </MUIClickAwayListener>;
}

ClickAwayListener.defaultProps = {
}

ClickAwayListener.propTypes = {
    /**
     * The wrapped element.
     */
    children: PropTypes.any,

    /**
     * If `true`, the React tree is ignored and only the DOM tree is considered.
     * This prop changes how portaled elements are handled.
     */
    disableReactTree: PropTypes.bool,

    /**
     * The mouse event to listen to. You can disable the listener by providing `false`.
     */
    mouseEvent: PropTypes.oneOf([false, "onClick", "onMouseDown", "onMouseUp"]),

    /**
     * The touch event to listen to. You can disable the listener by providing `false`.
     */
    touchEvent: PropTypes.oneOf([false, "onTouchEnd", "onTouchStart"]),

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default ClickAwayListener;
