import { Snackbar as MUISnackbar } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Snackbar.
*/
const Snackbar = (props) => {
    const {
        children,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUISnackbar {...propsToForward}
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    >
        {children}
    </MUISnackbar>;
}

Snackbar.defaultProps = {
    n_clicks: 0,
}

Snackbar.propTypes = {
    /**
     * The action to display. It renders after the message, at the end of the snackbar.
     */
    action: PropTypes.any,

    /**
     * The anchor of the `Snackbar`.
     * On smaller screens, the component grows to occupy all the available width,
     * the horizontal alignment is ignored.
     */
    anchorOrigin: PropTypes.any,

    /**
     * The number of milliseconds to wait before automatically calling the
     * `onClose` function. `onClose` should then set the state of the `open`
     * prop to hide the Snackbar. This behavior is disabled by default with
     * the `null` value.
     */
    autoHideDuration: PropTypes.number,

    /**
     * Replace the `SnackbarContent` component.
     */
    children: PropTypes.any,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * Props applied to the `ClickAwayListener` element.
     */
    ClickAwayListenerProps: PropTypes.any,

    /**
     * Props applied to the [`SnackbarContent`](/api/snackbar-content/) element.
     */
    ContentProps: PropTypes.any,

    /**
     * If `true`, the `autoHideDuration` timer will expire even if the window is not focused.
     */
    disableWindowBlurListener: PropTypes.bool,

    /**
     * When displaying multiple consecutive Snackbars from a parent rendering a single
     * <Snackbar/>, add the key prop to ensure independent treatment of each message.
     * e.g. <Snackbar key={message} />, otherwise, the message may update-in-place and
     * features such as autoHideDuration may be canceled.
     */
    key: PropTypes.any,

    /**
     * The message to display.
     */
    message: PropTypes.any,

    /**
     * If `true`, the component is shown.
     */
    open: PropTypes.bool,

    /**
     * The number of milliseconds to wait before dismissing after user interaction.
     * If `autoHideDuration` prop isn't specified, it does nothing.
     * If `autoHideDuration` prop is specified but `resumeHideDuration` isn't,
     * we default to `autoHideDuration / 2` ms.
     */
    resumeHideDuration: PropTypes.number,

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The component used for the transition.
     * [Follow this guide](/components/transitions/#transitioncomponent-prop) to learn more about the requirements for this component.
     */
    TransitionComponent: PropTypes.any,

    /**
     * The duration for the transition, in milliseconds.
     * You may specify a single timeout for all transitions, or individually with an object.
     */
    transitionDuration: PropTypes.oneOfType([PropTypes.number, PropTypes.any]),

    /**
     * Props applied to the transition element.
     * By default, the element is based on this [`Transition`](https://reactcommunity.org/react-transition-group/transition) component.
     */
    TransitionProps: PropTypes.any,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     */
    slot: PropTypes.string,

    /**
     */
    title: PropTypes.string,

    /**
     */
    defaultChecked: PropTypes.bool,

    /**
     */
    defaultValue: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any]),

    /**
     */
    suppressContentEditableWarning: PropTypes.bool,

    /**
     */
    suppressHydrationWarning: PropTypes.bool,

    /**
     */
    accessKey: PropTypes.string,

    /**
     */
    contentEditable: PropTypes.oneOf([false, true, "true", "false", "inherit"]),

    /**
     */
    contextMenu: PropTypes.string,

    /**
     */
    dir: PropTypes.string,

    /**
     */
    draggable: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    hidden: PropTypes.bool,

    /**
     */
    id: PropTypes.string,

    /**
     */
    lang: PropTypes.string,

    /**
     */
    placeholder: PropTypes.string,

    /**
     */
    spellCheck: PropTypes.oneOf([false, true, "true", "false"]),

    /**
     */
    tabIndex: PropTypes.number,

    /**
     */
    translate: PropTypes.oneOf(["yes", "no"]),

    /**
     */
    radioGroup: PropTypes.string,

    /**
     */
    role: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf(["article", "button", "dialog", "figure", "form", "img", "link", "main", "menu", "menuitem", "option", "table", "switch", "alert", "alertdialog", "application", "banner", "cell", "checkbox", "columnheader", "combobox", "complementary", "contentinfo", "definition", "directory", "document", "feed", "grid", "gridcell", "group", "heading", "list", "listbox", "listitem", "log", "marquee", "math", "menubar", "menuitemcheckbox", "menuitemradio", "navigation", "none", "note", "presentation", "progressbar", "radio", "radiogroup", "region", "row", "rowgroup", "rowheader", "scrollbar", "search", "searchbox", "separator", "slider", "spinbutton", "status", "tab", "tablist", "tabpanel", "term", "textbox", "timer", "toolbar", "tooltip", "tree", "treegrid", "treeitem"])]),

    /**
     */
    about: PropTypes.string,

    /**
     */
    datatype: PropTypes.string,

    /**
     */
    inlist: PropTypes.any,

    /**
     */
    prefix: PropTypes.string,

    /**
     */
    property: PropTypes.string,

    /**
     */
    resource: PropTypes.string,

    /**
     */
    typeof: PropTypes.string,

    /**
     */
    vocab: PropTypes.string,

    /**
     */
    autoCapitalize: PropTypes.string,

    /**
     */
    autoCorrect: PropTypes.string,

    /**
     */
    autoSave: PropTypes.string,

    /**
     */
    color: PropTypes.string,

    /**
     */
    itemProp: PropTypes.string,

    /**
     */
    itemScope: PropTypes.bool,

    /**
     */
    itemType: PropTypes.string,

    /**
     */
    itemID: PropTypes.string,

    /**
     */
    itemRef: PropTypes.string,

    /**
     */
    results: PropTypes.number,

    /**
     */
    security: PropTypes.string,

    /**
     */
    unselectable: PropTypes.oneOf(["on", "off"]),

    /**
     * Hints at the type of data that might be entered by the user while editing the element or its contents
     */
    inputMode: PropTypes.oneOf(["text", "none", "search", "tel", "url", "email", "numeric", "decimal"]),

    /**
     * Specify that a standard HTML element should behave like a defined custom built-in element
     */
    is: PropTypes.string,

    /**
     */
    dangerouslySetInnerHTML: PropTypes.any,

    /**
     */
    ref: PropTypes.any,

    /**
     * An integer that represents the number of times that this element has been clicked on.
     */
    n_clicks: PropTypes.number,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Snackbar;
