import { StyledEngineProvider as MUIStyledEngineProvider } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI StyledEngineProvider.
*/
const StyledEngineProvider = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIStyledEngineProvider {...propsToForward}
    >
        {children}
    </MUIStyledEngineProvider>;
}

StyledEngineProvider.defaultProps = {
}

StyledEngineProvider.propTypes = {
    /**
     */
    children: PropTypes.node,

    /**
     */
    injectFirst: PropTypes.bool,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default StyledEngineProvider;
