import { Chip as MUIChip } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';
import { Icon } from '@mui/material';
import { Avatar } from '@mui/material';

/**
* Material-UI Chip.
*/
const Chip = (props) => {
    const {
        n_clicks,
        n_deletes,
        icon,
        deleteIcon,
        avatar,
        setProps,
        ...propsToForward
    } = props;

    if (icon) { propsToForward.icon = <Icon>{icon}</Icon>; }
    if (typeof deleteIcon === 'string') { propsToForward.deleteIcon = <Icon>{deleteIcon}</Icon>; }
    if (avatar) { propsToForward.avatar = <Avatar {...avatar} />; }
    if (propsToForward.clickable) { propsToForward.onClick = () => setProps({ n_clicks: n_clicks + 1 }); }
    if (deleteIcon) { propsToForward.onDelete = () => setProps({ n_deletes: n_deletes + 1 }); }

    return <MUIChip {...propsToForward}
    />
}

Chip.defaultProps = {
    n_clicks: 0,
    n_deletes: 0,
    deleteIcon: false,
}

Chip.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * The Avatar element to display.
     */
    avatar: PropTypes.any,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * If `true`, the chip will appear clickable, and will raise when pressed,
     * even if the onClick prop is not defined.
     * If `false`, the chip will not appear clickable, even if onClick prop is defined.
     * This can be used, for example,
     * along with the component prop to indicate an anchor Chip is clickable.
     * Note: this controls the UI and does not affect the onClick event.
     */
    clickable: PropTypes.bool,

    /**
     * The color of the component. It supports those theme colors that make sense for this component.
     */
    color: PropTypes.oneOf(["success", "info", "warning", "error", "primary", "secondary", "default"]),

    /**
     * Override the default delete icon element. Shown only if `onDelete` is set.
     */
    deleteIcon: PropTypes.any,

    /**
     * If `true`, the component is disabled.
     */
    disabled: PropTypes.bool,

    /**
     * Icon element.
     */
    icon: PropTypes.any,

    /**
     * The content of the component.
     */
    label: PropTypes.any,

    /**
     * The size of the component.
     */
    size: PropTypes.oneOf(["small", "medium"]),

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The variant to use.
     */
    variant: PropTypes.oneOf(["outlined", "filled"]),

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * An integer that represents the number of times that this element has been clicked on.
     */
    n_clicks: PropTypes.number,

    /**
     * An integer that represents the number of times that the delete icon has been clicked on.
     */
    n_deletes: PropTypes.number,

    /**
     * The name of the icon to show.
     */
    icon: PropTypes.string,

    /**
     * If `False`, does not show the delete icon. If `True`, shows the default delete icon. If a string is passed, it is used as the name of the icon to show.
     */
    deleteIcon: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),

    /**
     * The Avatar element to display.
     */
    avatar: PropTypes.exact({ src: PropTypes.string, children: PropTypes.string }),

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default Chip;
