    ActionsComponent: PropTypes.oneOfType([PropTypes.any, PropTypes.oneOf(["object", "div", "abbr", "address", "article", "aside", "b", "bdi", "bdo", "big", "blockquote", "caption", "cite", "code", "col", "colgroup", "dd", "del", "details", "dfn", "dt", "em", "embed", "figcaption", "figure", "footer", "h1", "h2", "h3", "h4", "h5", "h6", "head", "header", "hgroup", "hr", "i", "iframe", "img", "input", "kbd", "keygen", "legend", "main", "mark", "menu", "menuitem", "meter", "nav", "noindex", "noscript", "output", "p", "picture", "rp", "rt", "ruby", "s", "samp", "section", "small", "span", "strong", "sub", "summary", "sup", "table", "tbody", "td", "tfoot", "th", "thead", "time", "tr", "u", "var", "wbr", "webview"])]),

