import { BadgeUnstyled as MUIBadgeUnstyled } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI BadgeUnstyled.
*/
const BadgeUnstyled = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIBadgeUnstyled {...propsToForward}
    >
        {children}
    </MUIBadgeUnstyled>;
}

BadgeUnstyled.defaultProps = {
}

BadgeUnstyled.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * The anchor of the badge.
     */
    anchorOrigin: PropTypes.any,

    /**
     * The components used for each slot inside the Badge.
     * Either a string to use a HTML element or a component.
     */
    components: PropTypes.any,

    /**
     * The props used for each slot inside the Badge.
     */
    componentsProps: PropTypes.any,

    /**
     * Wrapped shape the badge should overlap.
     */
    overlap: PropTypes.oneOf(["circular", "rectangular"]),

    /**
     * The content rendered within the badge.
     */
    badgeContent: PropTypes.any,

    /**
     * The badge will be added relative to this node.
     */
    children: PropTypes.node,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * If `true`, the badge is invisible.
     */
    invisible: PropTypes.bool,

    /**
     * Max count to show.
     */
    max: PropTypes.number,

    /**
     * Controls whether the badge is hidden when `badgeContent` is zero.
     */
    showZero: PropTypes.bool,

    /**
     * The variant to use.
     */
    variant: PropTypes.string,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default BadgeUnstyled;
