import { OverridableCardHeader as MUIOverridableCardHeader } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI OverridableCardHeader.
*/
const OverridableCardHeader = (props) => {
    const {
        setProps,
        ...propsToForward
    } = props;


    return <MUIOverridableCardHeader {...propsToForward}
    />
}

OverridableCardHeader.defaultProps = {
}

OverridableCardHeader.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * The action to display in the card header.
     */
    action: PropTypes.any,

    /**
     * The Avatar element to display.
     */
    avatar: PropTypes.any,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * If `true`, `subheader` and `title` won't be wrapped by a Typography component.
     * This can be useful to render an alternative Typography variant by wrapping
     * the `title` text, and optional `subheader` text
     * with the Typography component.
     */
    disableTypography: PropTypes.bool,

    /**
     * The content of the component.
     */
    subheader: PropTypes.any,

    /**
     * These props will be forwarded to the subheader
     * (as long as disableTypography is not `true`).
     */
    subheaderTypographyProps: PropTypes.any,

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The content of the component.
     */
    title: PropTypes.any,

    /**
     * These props will be forwarded to the title
     * (as long as disableTypography is not `true`).
     */
    titleTypographyProps: PropTypes.any,

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default OverridableCardHeader;
