import { ImageList as MUIImageList } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI ImageList.
*/
const ImageList = (props) => {
    const {
        children,
        setProps,
        ...propsToForward
    } = props;


    return <MUIImageList {...propsToForward}
    >
        {children}
    </MUIImageList>;
}

ImageList.defaultProps = {
}

ImageList.propTypes = {
    /**
     * The component used for the root node.
     * Either a string to use a HTML element or a component.
     */
    component: PropTypes.string,

    /**
     * The content of the component, normally `ImageListItem`s.
     */
    children: PropTypes.node,

    /**
     * Override or extend the styles applied to the component.
     */
    classes: PropTypes.any,

    /**
     * Number of columns.
     */
    cols: PropTypes.number,

    /**
     * The gap between items in px.
     */
    gap: PropTypes.number,

    /**
     * The height of one row in px.
     */
    rowHeight: PropTypes.oneOfType([PropTypes.number, PropTypes.oneOf(["auto"])]),

    /**
     * The system prop that allows defining system overrides as well as additional CSS styles.
     */
    sx: PropTypes.object,

    /**
     * The variant to use.
     */
    variant: PropTypes.oneOf(["standard", "masonry", "quilted", "woven"]),

    /**
     */
    className: PropTypes.string,

    /**
     */
    style: PropTypes.any,

    /**
     * The ID of this component, used to identify dash components in callbacks.
The ID needs to be unique across all of the components in an app.
     */
    id: PropTypes.string,


    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,
}

export default ImageList;
