import { Rating as MUIRating } from '@mui/material';
import PropTypes from 'prop-types';
import React from 'react';

/**
* Material-UI Rating.
*/
const Rating = (props) => {
    const {
        persistence,
        persisted_props,
        persistence_type,
        n_clicks,
        setProps,
        ...propsToForward
    } = props;


    return <MUIRating {...propsToForward}
        onChange={ (e, value) => setProps({ value }) }
        onClick={ () => setProps({ n_clicks: n_clicks + 1 }) }
    />
}

